import type {FC} from 'react'
import React, {useCallback} from 'react'

import {AppRoutes, useNavigationState} from '@bpinc/ad-navigation-state-context'
import {useSelectEvent} from '@bpinc/cal-hooks'

import {CommonOpenEventButton} from './CommonOpenEventButton'
import type {OpenEventButtonCommonProps} from './OpenEventButton'


export const AdOpenEventButton: FC<OpenEventButtonCommonProps> = ({eventId, onClick}) => {
    const goTo = useNavigationState('goTo')
    const selectEvent = useSelectEvent()

    const handleOpen = useCallback(() => {
        if (eventId) {
            goTo(AppRoutes.calendar)
            selectEvent({id: eventId})
        }

        onClick()
    }, [eventId, goTo, onClick, selectEvent])

    return (
        <CommonOpenEventButton onClick={handleOpen}/>
    )
}
